      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" :disabled="btnStatus.addShow" class="addBtn" size="mini" v-if="authority.b_edit==1">{{ isNeedHide ? "增行" : "新增"}}</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" :disabled="btnStatus.delShow" v-if="authority.b_del==1" v-show="!isNeedHide">删除</el-button>
        <el-button plain icon="el-icon-delete" @click="deletesRow" size="mini" :disabled="btnStatus.delShow" v-if="authority.b_del==1" v-show="isNeedHide">删行</el-button>
        <el-button plain icon="el-icon-edit-outline" @click="untilList" :disabled="btnStatus.untilShow" size="mini" v-if="authority.b_edit==1">修改</el-button>
        <el-button plain icon="el-icon-tickets" @click="saveList('formDom')" size="mini" :disabled="btnStatus.saveShow">保存</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="audit" size="mini" :disabled="btnStatus.examine" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="cancellation" size="mini" :disabled="btnStatus.abandon" v-if="authority.b_invalid==1">作废</el-button>
        <el-button plain icon="el-icon-download" size="mini" @click="downLoadFile">下载模板</el-button>
        <el-button plain icon="iconfont icon-daoru5" @click="dialogEmployee" v-if="authority.b_import==1" size="mini">导入</el-button>
        <el-button @click="cancel" plain icon="el-icon-error" size="mini" v-if="isCancel">取消</el-button>
      </div>

      <div class="table_container purchase_price">
        <el-form :rules="TableData.rules" :model="TableData" ref="formDom">
          <el-table :highlight-current-row="true" tooltip-effect="light" border v-loading="loading1" :data="TableData.tableData" max-height="600" style="width:100%" @selection-change="handleSelectionChange" :cell-style="initStyle">
            <el-table-column :show-overflow-tooltip="true" type="selection" align="center" width="44">
            </el-table-column>
            <el-table-column label="序号" type="index" width="46" align="center"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="company_name" min-width="120" label="采购组织" :render-header="renderHeader">
              <template slot-scope="scope">
                <el-form-item ref="goods_name" :prop="'tableData.' + scope.$index + '.company_id'" :rules='TableData.rules.company_id' v-if="scope.row.edit && addType!='N' " class="form-container-purchasepriceindex">
                  <el-select v-model="scope.row.company_id" :loading="tableDownLoading" placeholder="请选择" @change="changeCpmpany(scope.row,scope.$index)" filterable @focus="event => portFocusCompanyList('', scope.row)" :class="{purchasepriceIndexRed:scope.$index==tipsIndex}">
                    <el-option v-for="item in scope.row.companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                      <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
                    </el-option>
                  </el-select>
                </el-form-item>
                <span v-else>{{ scope.row.company_name}}</span>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="goods_id" min-width="150" label="商品编号" :render-header="renderHeader">
              <template slot-scope="scope">
                <span :class="{purchasepriceIndexRed: scope.$index == tipsIndex}">{{scope.row.goods_id}}</span>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称" :render-header="renderHeader">
              <template slot-scope="scope">
                <v-GoodsList v-if="scope.row.edit && addType!='N' " :scopes='scope' :vxeParams='{company_id: scope.row.company_id}' :changeGoods='handleSelect' :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
                <!-- <pre>{{scope.row.goods_name}}</pre> -->
                <!-- <el-form-item ref="goods_name" :prop="'tableData.' + scope.$index + '.goods_name'" :rules='TableData.rules.goods_name' v-if="scope.row.edit && addType!='N' " class="form-container-purchasepriceindex product-name">
                  <el-select v-if="scope.row.edit && addType!='N'" v-model="scope.row.goods_name" @focus="focusGoods(scope.row)" @change="changeGoods(scope.row,scope.$index)" value-key="goods_id" filterable remote clearable reserve-keyword placeholder="请输入" :loading="loading" :remote-method="(query)=>{remoteMethod(scope.row,query)}" :class="{purchasepriceIndexRed:  scope.$index == tipsIndex}">
                    <ul class="goods_ul goods_ul2 goods_ul3">
                      <li>商品编号</li>
                      <li>商品名称</li>
                    </ul>
                    <el-option v-for="item in goodsList" :key="item.$index" :label="item.goods_name" :value="item">
                      <ul class="goods_ul  goods_ul3">
                        <li style="color: #8492a6; font-size: 13px">{{ item.goods_id }}</li>
                        <li>{{ item.goods_name }}</li>
                      </ul>
                    </el-option>
                  </el-select>
                </el-form-item>-->
                <span v-else>{{scope.row.goods_name}}</span>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="price" min-width="100" label="采购价格" :render-header="renderHeader">
              <template slot-scope="scope">
                <el-form-item ref="price" :prop="'tableData.' + scope.$index + '.price'" :rules='TableData.rules.price' v-if="scope.row.edit && ( addType=='Y' || scope.row.auditor_status==1) " class="form-container-purchasepriceindex">
                  <el-input placeholder="请输入" v-model="scope.row.price" style="width:100%"></el-input>
                </el-form-item>
                <span v-else>{{ scope.row.price | priceFormatNone}}</span>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="start_time" min-width="170" width="170" label="政策生效日期" :render-header="renderHeader">
              <template slot-scope="scope">
                <el-form-item ref="goods_name" :prop="'tableData.' + scope.$index + '.start_time'" :rules='TableData.rules.start_time' v-if="scope.row.edit && ( addType=='Y' || scope.row.auditor_status==1) " class="form-container-purchasepriceindex">
                  <el-date-picker v-if="scope.row.edit" v-model="scope.row.start_time" value-format="yyyy-MM-dd" type="date" placeholder="选择日期" :clearable="false" :class="{purchasepriceIndexRed:scope.$index == tipsIndex}"></el-date-picker>
                </el-form-item>
                <span v-else>{{ scope.row.start_time}}</span>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_user_name" min-width="80" label="创建人">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_time" min-width="170" label="创建时间">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" min-width="80" label="审核状态">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="auditor_name" min-width="80" label="审核人">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="auditor_time" min-width="170" label="审核时间">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="update_user_name" min-width="80" label="修改人">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="update_time" min-width="170" label="修改时间">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="remark" min-width="80" label="备注">
              <template slot-scope="scope">
                <el-input v-if="scope.row.edit" v-model="scope.row.remark" placeholder="请输入"></el-input>
                <span v-else>{{ scope.row.remark}}</span>
              </template>
            </el-table-column>
          </el-table>
        </el-form>
        <upload :options="options" @receiveUploadMsg="receiveUploadMsg" ref="upload"></upload>

      </div>
      <div class="page_container" v-if="!isNeedHide">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "purchasePriceIndex",
  data() {
    const checkNumPot2 = (rule, value, callback) => {
      const reg =
        /(^[0-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
      if (!reg.test(value)) {
        let index = parseFloat(rule.field[10]); //得到索引
        this.indexArr.push(index);
        this.indexArr = [...new Set(this.indexArr)];
        return callback(new Error("请填非0数字,最多2位小数"));
      } else {
        callback();
      }
    };
    const kong = (rule, value, callback) => {
      if (!value) {
        let index = parseFloat(rule.field[10]); //得到索引
        this.indexArr.push(index);
        this.indexArr = [...new Set(this.indexArr)];
        return callback(new Error("输入不能为空"));
      } else {
        callback();
      }
    };
    return {
      tableDownLoading: false, // table 列表下拉
      options: {
        title: "采购价信息导入",
        url: "/purchase/price/importPrice",
      },
      indexArr: [], //错误索引
      params: {
        auditor_status: 0,
      },
      TableData: {
        tableData: [],
        total: 10,
        rules: {
          price: [{ validator: checkNumPot2, trigger: "blur" }],
          goods_name: [{ validator: kong, trigger: "blur" }],
          start_time: [{ validator: kong, trigger: "blur" }],
          company_id: [{ validator: kong, trigger: ["blur", "change"] }],
        },
      },
      listLength: 0, //列表长度
      isCancel: false,
      btnStatus: {
        saveShow: true, //保存按钮
        addShow: false, //新增按钮
        untilShow: true, //修改
        delShow: true, //删除按钮
        abandon: true, //作废按钮
        examine: true, //审核按钮
      },
      addType: "", //新增or修改  Y 新增
      isNeedHide: false, //隐藏搜索区域
      multipleSelection: [], //复选框,
      multipleSelectionIndex: 0, //新增的索引
      getIndex: [],
      tipsIndex: null,
    };
  },
  methods: {
    ...mapActions(["settingPurchaseACT", "purchasepriceIndexACT"]),
    // 接口table列表组织下拉
    portFocusCompanyList(parameter, row) {
      this.tableDownLoading = true; // 开启加载
      this.$axMethod
        .getCompanyListDefalut({
          company_name: parameter,
        })
        .then(({ data }) => {
          this.tableDownLoading = false; // 关闭加载
          let result = data.data.company_list;
          if (result instanceof Array) {
            result.unshift({
              company_name: data.data.company_name,
              company_id: data.data.company_id,
              is_choose: data.data.is_choose,
              type: 1,
            });
            this.$set(
              row,
              "companyList",
              result.filter((item) => {
                return item.is_choose == 1; // 是否可以被选择 1可以 0不可以
              })
            );
          }
        })
        .catch((error) => {
          this.tableDownLoading = false; // 关闭加载
        });
    },
    brandChange(val) {
      if (val) {
        this.params.brand_name = this.brandList.filter(
          (obj) => obj.brand_id === val
        )[0].brand_name;
      }
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
    },
    cascaderMethod(value) {
      this.params.goods_type_id = value[2];
    },
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    receiveUploadMsg(response) {
      getData(this.searchParams);
    },
    initStyle({ row, column, rowIndex, columnIndex }) {
      if (this.indexArr.includes(rowIndex)) {
        return "vertical-align:top;padding-top:5px;";
      }
    },
    handleExceed(files, fileList) {
      this.$message({
        message: "一次只能上传一个文件！",
        type: "error",
      });
    },
    downLoadFile() {
      // 下载模板
      this.$axMethod.purchaseDownloadFileUrl().then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    onReset() {
      this.$refs.cascader.clear();
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        auditor_status: 0,
      });
      opthionList(this, localStorage);
      this.timeValue = [];
      this.params.start_time = "";
      this.params.end_time = "";
      this.$set(this, "searchParams", {
        params: this.params, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getGoodsPriceList", //接口名
        vm: this, //传递的this
      });
      this.onSearch();
    },
    onSearch() {
      //查询
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      this.purchasepriceIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
      });
      getData(this.searchParams);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
    changeCpmpany(row, index, type) {
      this.$set(row, "goods_id", "");
      this.$set(row, "goods_name", "");
    },
    addF() {
      //清空标记
      this.tipsIndex = null;
      this.TableData.length = this.TableData.tableData.length;
      this.btnStatus = {
        saveShow: false, //保存按钮
        addShow: false, //新增按钮
        untilShow: true, //修改
        delShow: false, //删除按钮
        abandon: true, //作废按钮
        examine: true, //审核按钮
      };
      this.isNeedHide = true;
      //取消按钮显示
      this.isCancel = true;
      this.addType = "Y";
      this.TableData.tableData.unshift({
        edit: true,
        company_id: Number(localStorage.getItem("company_id")),
        company_name: localStorage.getItem("company_name"),
        companyList: [
          {
            company_id: Number(localStorage.getItem("company_id")),
            company_name: localStorage.getItem("company_name"),
          },
        ],
        start_time: this.$publicFution.formatData(new Date()),
        add_user_name: localStorage.getItem("realname"),
        auditor_status_name: "未审核",
        Index: this.multipleSelectionIndex++, //通过索引去执行新增中的删行操作
      });
    },
    untilList() {
      let dataArr = this.multipleSelection;
      //未审核
      let hasOne = dataArr.some((item, index) => {
        return item.auditor_status == 1;
      });
      //已审核
      let hasTwo = dataArr.some((item, index) => {
        return item.auditor_status == 2;
      });
      //作废
      let hasThree = dataArr.some((item, index) => {
        return item.auditor_status == 3;
      });
      if (hasTwo && !hasThree && !hasOne) {
        this.$message.error("已审核的单据不可以修改，请重新勾选");
        return false;
      }
      if (!hasTwo && hasThree && !hasOne) {
        this.$message.error("作废的单据不可以修改，请重新勾选");
        return false;
      }
      if (hasTwo && hasThree && !hasOne) {
        this.$message.error("审核的单据均不可以修改，请重新勾选");
        return false;
      }
      //修改
      this.addType = "N";
      if (this.multipleSelection.length) {
        this.isNeedHide = true;
        this.btnStatus = {
          saveShow: false, //保存按钮
          addShow: true, //新增按钮
          untilShow: false, //修改
          delShow: false, //删除按钮
          abandon: true, //作废按钮
          examine: true, //审核按钮
        };
        //取消按钮显示
        this.isCancel = true;
        let tableData = this.TableData.tableData.length;
        for (let a = 0; a < tableData; a++) {
          if (this.getIndex.indexOf(a) != -1) {
            //对比性能前后时间相差15秒左右
            this.$set(this.TableData.tableData[a], "edit", true);
            // this.TableData.tableData[a].edit = true;
          }
        }
      } else {
        this.$message({
          message: "请选择需要修改的信息",
          type: "warning",
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      if (val.length) {
        this.btnStatus = {
          saveShow: true, //保存按钮
          addShow: true, //新增按钮
          untilShow: false, //修改
          delShow: false, //删除按钮
          abandon: false, //作废按钮
          examine: false, //审核按钮
        };
        //如果是修改状态复选框变化,作废、审核按钮禁用
        if (this.isNeedHide) {
          this.btnStatus = {
            saveShow: false, //保存按钮
            addShow: true, //新增按钮
            untilShow: false, //修改
            delShow: false, //删除按钮
            abandon: true, //作废按钮
            examine: true, //审核按钮
          };
        }
        //新增状态
        if (this.TableData.tableData.length - this.listLength > 0) {
          this.btnStatus = {
            saveShow: false, //保存按钮
            addShow: false, //新增按钮
            untilShow: true, //修改
            delShow: false, //删除按钮
            abandon: true, //作废按钮
            examine: true, //审核按钮
          };
        }
      } else {
        this.btnStatus = {
          saveShow: true, //保存按钮
          addShow: false, //新增按钮
          untilShow: true, //修改
          delShow: true, //删除按钮
          abandon: true, //作废按钮
          examine: true, //审核按钮
        };
        //没选中但是新增状态没退出的情况下
        if (this.TableData.tableData.length - this.listLength > 0) {
          this.btnStatus = {
            saveShow: false, //保存按钮
            addShow: false, //新增按钮
            untilShow: true, //修改
            delShow: true, //删除按钮
            abandon: true, //作废按钮
            examine: true, //审核按钮
          };
        }
      }
      let aRR = [];
      val.forEach((val, index) => {
        this.TableData.tableData.forEach((v, i) => {
          //唯一id属性来得到索引值
          if (val.price_id == v.price_id) {
            aRR.push(i); // id 是每一行的数据id
          }
        });
      });
      this.getIndex = aRR;
      aRR = aRR.sort();
      if (localStorage.getItem("aRR") != null) {
        //复选框点击上一次的数组
        var lastArr = JSON.parse(localStorage.getItem("aRR")).aRR;
      }
      //当前的数组
      localStorage.setItem("aRR", JSON.stringify({ aRR }));
      if (this.addType == "N") {
        let tableData = this.TableData.tableData;
        if (!aRR.length) {
          for (let b = 0; b < tableData.length; b++) {
            this.$set(tableData[b], "edit", false);
          }
        }
        for (let c = 0; c < aRR.length; c++) {
          this.$set(tableData[aRR[c]], "edit", true);
        }
        if (aRR.length - lastArr.length < 0) {
          let a = new Set(aRR);
          let d = new Set(lastArr);
          let difference = new Set([...d].filter((x) => !a.has(x)));
          let differenceArr = [...difference];
          for (let n = 0; n < differenceArr.length; n++) {
            this.$set(tableData[differenceArr[n]], "edit", false);
          }
        }
        //sconsole.log(lastArr);
      }
    },
    saveList(formDom) {
      //赋值前先恢复默认值
      let flag = false;
      this.tipsIndex = null;
      this.$refs[formDom].validate((valid, model) => {
        if (valid) {
          flag = true;
          this.indexArr = [];
        } else {
          flag = false;
          this.$message.error("请输入合法的内容");
        }
      });
      //校验
      if (flag) {
        //保存方法
        var newArr = this.TableData.tableData.filter((v) => {
          return v.edit == true;
        });
        if (!newArr.length) {
          this.$message.warning("当前您没有编辑的数据！");
          return false;
        }
        let dataArr = this.TableData.tableData;
        //可编辑的那几项
        let editArr = dataArr.filter((x) => {
          return x.edit;
        });
        //新增判断重复的数组
        var arr = [];
        //通过对象属性名的值拼接字符串来比较需要比较的属性
        for (let i = 0; i < editArr.length; i++) {
          if (this.addType == "Y" || this.addType == "N") {
            arr.push(
              `${editArr[i].company_id}${editArr[i].goods_id}${editArr[i].price}${editArr[i].start_time}`
            );
          }
        }
        let handlerArr = [];
        if (this.addType == "Y") {
          for (var i = 0; i < newArr.length; i++) {
            handlerArr.push({
              price_id: 0, //新增传默认值0
              company_id: newArr[i].company_id,
              price: newArr[i].price,
              start_time: newArr[i].start_time,
              remark: newArr[i].remark,
              goods_id: newArr[i].goods_id,
            });
          }
          //得到最后一个重复项的index
          let getIndexArr = [];
          for (let a = 0; a < arr.length; a++) {
            if (getIndexArr.includes(arr[a])) {
              this.tipsIndex = a;
              this.$message({
                message: "与已新增数据重复，请修改后重新保存",
                type: "warning",
              });
              return false;
            } else {
              getIndexArr.push(arr[a]);
              this.tipsIndex = null;
            }
          }
        } else {
          for (var i = 0; i < newArr.length; i++) {
            handlerArr.push({
              price_id: newArr[i].price_id,
              company_id: newArr[i].company_id,
              price: newArr[i].price,
              start_time: newArr[i].start_time,
              remark: newArr[i].remark,
              goods_id: newArr[i].goods_id,
            });
          }
        }
        if (!this.tipsIndex) {
          this.$axMethod
            .savePrice({
              price_data: JSON.stringify(handlerArr),
            })
            .then((res) => {
              let { code, msg } = res.data;
              if (code == 200) {
                this.$message({
                  message: res.data.msg,
                  type: "success",
                });
                this.btnStatus = {
                  saveShow: true, //保存按钮
                  addShow: false, //新增按钮
                  untilShow: true, //修改
                  delShow: true, //删除按钮
                  abandon: true, //作废按钮
                  examine: true, //审核按钮
                };
                this.isNeedHide = false;
                this.isCancel = false;
                this.tipsIndex = null;
                this.addType = "";
                localStorage.removeItem("aRR");
                getData(this.searchParams);
              }
              if (code == 100) {
                this.$message.error(
                  "您已提交，切勿重新提交，如需修改，请在已提交的数据中修改"
                );
                let firstItem = msg[0];
                console.log(msg[0]);

                console.log(
                  `${firstItem.company_id}${firstItem.goods_id}${firstItem.price}${firstItem.start_time}`
                );
                console.log(arr);
                // console.log(arr);
                if (this.addType == "Y") {
                  this.tipsIndex = arr.findIndex((x) => {
                    return (
                      x ==
                      `${firstItem.company_id}${firstItem.goods_id}${firstItem.price}${firstItem.start_time}`
                    );
                  });
                  console.log(this.tipsIndex);
                }
                if (this.addType == "N") {
                  let tableData = this.TableData.tableData;
                  this.tipsIndex = tableData.findIndex((x) => {
                    return (
                      `${firstItem.start_time}${firstItem.goods_id}` ==
                        `${x.start_time}${x.goods_id}` && x.auditor_status != 2
                    );
                  });
                }
              }
            });
        }
      }
    },
    audit() {
      let shenheArr = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        shenheArr.push(this.multipleSelection[i].price_id);
      }
      let funcan = {
        eachData: {
          price_id: shenheArr.toString(),
        },
        UrlApi: "auditorPrice",
        list: {
          UrlApi: "getGoodsPriceList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      this.$common.audit(funcan);
    },
    cancellation() {
      let shenheArr = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        shenheArr.push(this.multipleSelection[i].price_id);
      }
      let funcan = {
        eachData: {
          price_id: shenheArr.toString(),
        },
        UrlApi: "priceauditorInvalid",
        list: {
          UrlApi: "getGoodsPriceList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      this.$common.audit(funcan);
    },
    beforeDeleteJudge() {
      return this.multipleSelection.some((x) => {
        let str = x.auditor_status_name;
        return str.includes("作废") || str.includes("已审核");
      });
    },
    deletes() {
      //删除操作调接口
      var deledata = {
        deleID: "price_id",
        deleSelect: this.multipleSelection,
        deleApi: "delPrice",
        list: {
          UrlApi: "getGoodsPriceList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      this.$common.deletes(deledata, "删除", this);
    },
    deletesRow() {
      if (this.addType == "Y") {
        let dataList = this.TableData.tableData;
        if (this.multipleSelection.length) {
          let indexArr = [];
          this.multipleSelection.map((x) => {
            indexArr.push(x.Index);
          });
          for (let i = dataList.length - 1; i >= 0; i--) {
            if (
              indexArr.includes(dataList[i].Index) &&
              dataList[i].Index != undefined
            ) {
              dataList.splice(i, 1);
            }
          }
          //删行重置要不列表会变颜色
          this.tipsIndex = null;
        } else {
          this.$message({
            message: "请选择需要删除的行",
            type: "warning",
          });
        }
      }
      if (this.addType == "N") {
        //删除操作调接口
        var deledata = {
          deleID: "price_id",
          deleSelect: this.multipleSelection,
          deleApi: "delPrice",
          list: {
            UrlApi: "getGoodsPriceList",
            data: this.TableData,
            esData: this.searchParams.params,
          },
        };
        this.$common.deletes(deledata, "删除", this);
      }
    },
    cancel() {
      this.tipsIndex = null; //重置标红索引
      localStorage.removeItem("aRR"); //取消删除复选框选中本地记录
      this.addType = ""; //重置点击类型
      this.btnStatus = {
        saveShow: true, //保存按钮
        addShow: false, //新增按钮
        untilShow: true, //修改
        delShow: true, //删除按钮
        abandon: true, //作废按钮
        examine: true, //审核按钮
      };
      this.isCancel = !this.isCancel;
      this.isNeedHide = !this.isNeedHide;
      this.indexArr = [];
      let tableData = this.TableData.tableData;
      //增加取消操作（不影响取消操作，只是在本地操作数据）
      for (let i = tableData.length - 1; i >= 0; i--) {
        if (Object.keys(tableData[i]).includes("edit")) {
          tableData.splice(i, 1);
        }
      }
      this.$refs.cascader.clear();
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        auditor_status: 0,
      });
      this.$set(this, "searchParams", {
        params: this.params, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getGoodsPriceList", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams);
    },
    renderHeader(h, { column, $index }) {
      //新增修改显示必填项
      if (this.isNeedHide) {
        return (
          <span class="start" style="cursor:pointer">
            {column.label}
          </span>
        );
      } else {
        return <span> {column.label}</span>;
      }
    },
  },
  computed: {
    ...mapGetters(["purchasepriceIndexGet"]),
  },
  watch: {
    loading1(newVal, val) {
      this.listLength = this.TableData.tableData.length;
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "companyList", [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name,
      },
    ]);
    if (this.purchasepriceIndexGet && this.purchasepriceIndexGet.params) {
      if (this.purchasepriceIndexGet.params.start_time) {
        const { start_time, end_time } = this.purchasepriceIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (
        this.purchasepriceIndexGet.params.company_id == localStorage.company_id
      ) {
        this.purchasepriceIndexGet.params.company_name =
          localStorage.company_name;
      }
      opthionList(this, this.purchasepriceIndexGet.params);
      this.$set(this, "params", this.purchasepriceIndexGet.params);
      this.params.page = this.purchasepriceIndexGet.goPage;
      this.params.per_page = this.purchasepriceIndexGet.pageSize;
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        auditor_status: 0,
      });
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getGoodsPriceList", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams);
  },
};
</script>



